*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 62.5%; // 1rem = 10px, 10px/16px = 62.5%

  @media only screen and (max-width: $bp-1200) {
    font-size: 50%;
  }
}

body {
  font-family: Roboto-Regular-webfont;
}

.container {
  width: 105rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}




